body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  
  
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  font-family: 'Dancing Script', cursive;
  color: #33BFD8;
  /* font-family: 'Grechen Fuemen', cursive; */
}
#root{
  width: 100%;
  height: 100%;
}
.App{
  width: 100%;
  height: 100%;
  /* background-color: #33BFD8; */
  background-color: black;
}
.All{
  width: 50%;
  height: 100%;
  position: relative;
  margin: auto;
  background-color: black;
  color: #33BFD8;
}
.All>*{
  position: absolute;
}
.cell{
  background-color: black;
  border: 0px;
  
  height: 33.33333333%;
  border: 2px solid #33BFD8;
  /* cursor: url(./img/Ellipse\ 8.png) !important; */
  cursor: url("./img/Ellipse\ 8.png"), auto !important;
}
.cell>*{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  font-size: 30;
  margin: 0;
  font-family: 'Dancing Script', cursive;
  
}
.cell:disabled{
  color: #33BFD8;
}
.colum{
  left: 50%;
  transform: translateX(-50%);
}
.status{
  top: 50px;
}
.restart{
  bottom: 50px;
  color: #33BFD8;
  background-color: rgb(0, 0, 0);
  border: 0px;
}
.restart:hover{
  transform: translateX(-50%) scale(1.2);
  transition: transform .3s ease-out;
  
}
.player{
  top: 50%;
  transform: translateY(-50%);
}
.left{
  left: 50px;
}
.right{
  right: 50px;
}
.box{
  width: 300px !important;
  height: 300px;
  background-color: rgb(114, 114, 114);
  margin-left: 0 !important;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);

}
.sub-line{
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 33, 122, 0);
  z-index: -1;
}
.line-contain{
  width: 100%;
  height: 100%;
  position: relative;
  background-color: rgba(122, 120, 0, 0);
}
.temp{
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  width: 5px;
  height: 100%;
  /* transform: rotate(90deg); */

}
.line{
  position: absolute;
  background-color: rgb(255, 219, 16);
  width: 100%;
  height: 5px;
}
.line012{
  top: 50px;
  left: 0;
}
.line345{
  top: 150px;
  left: 0;
}
.line678{
  top: 250px;
  left: 0;
}
.line036{
  left: 50px;
  top: 0;
  width: 5px;
  height: 100%;
}
.line147{
  left: 150px;
  top: 0;
  width: 5px;
  height: 100%;
}
.line258{
  left: 250px;
  top: 0;
  width: 5px;
  height: 100%;
}
.line048{
  left: 50%;
  top: 50%;
  /* transform: translate(-50%, -50%) rotate(-45deg); */
  width: 5px;
  height: 100%;
}
.line246{
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  width: 5px;
  height: 100%;
}